import firebase from 'firebase/app';
import { SyntheticEvent } from 'react';

import { AuthFetch, AuthSuccess, AuthOut } from 'types/actions';
import { writeUserLastActive } from 'data/user';
import { analytics } from 'utils/analytics';

const login = () => {
  if (typeof firebase.auth === 'function') {
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().useDeviceLanguage();
    void firebase.auth().signInWithRedirect(provider);
  }
};

export const handleAuth = (e: SyntheticEvent): void => {
  let shouldCall = true;
  const callAndCancel = () => {
    if (shouldCall) {
      login();
      shouldCall = false;
    }
  };
  setTimeout(callAndCancel, 300);
  analytics.act()?.logEvent('pre-login', {
    targetText: e.target instanceof HTMLButtonElement ? e.target.innerText || e.target.title + ' (title)' : '',
    scrollTop: document.querySelector('main')?.scrollTop || null,
    event_callback: callAndCancel,
  });
};

export const handleAuthOut = (userID: string): void => {
  writeUserLastActive(userID);
  if (typeof firebase.auth === 'function') {
    void firebase.auth().signOut();
  }
};

export const trackAuthChange = ({
  fetching,
  success,
  out,
}: {
  fetching: () => AuthFetch;
  success: (userData: AuthSuccess['payload']) => AuthSuccess;
  out: () => AuthOut;
}): void => {
  fetching();
  firebase.auth().onAuthStateChanged((user) => {
    if (user && user.email) {
      success({
        id: user.uid,
        name: user.displayName || '',
        email: user.email,
        photoURL: user.photoURL || '',
      });
    } else {
      out();
    }
  });
};
